import React from "react";
import _data from "../../data";

export default class Screenshots extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      price: {}
    };
  }

  componentDidMount() {
    /**
     * Your ajax will goes here to get data then call setState
     */

    this.setState({
      price: _data.price
    });
  }

  render() {
    return (
      <React.Fragment>
        <section
          id="screenshots"
          className="screenshots-section ptb-100 gray-light-bg"
        >
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-md-8">
                <div className="section-heading text-center">
                  <h2>
                    Platform screenshots <br /> <span>Looks awesome</span>
                  </h2>
                  <p className="lead">
                  We want to give you the best experience, so that you can develop your full potential with various technologies such as artificial intelligence and augmented reality.
                  </p>
                </div>
              </div>
            </div>
            <div className="screen-slider-content mt-5">
              <div className="screen-carousel owl-carousel owl-theme dot-indicator">
                <img src="img/sc1.jpeg" className="img-fluid" alt="screenshots" />
                <img src="img/sc2.jpeg" className="img-fluid" alt="screenshots" />
                <img src="img/sc3.jpeg" className="img-fluid" alt="screenshots" />
                <img src="img/sc4.jpeg" className="img-fluid" alt="screenshots" />
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}
